import React, { useState, useEffect } from 'react';
import {
  Grid,
  Card,
  CardMedia,
  CardActionArea,
  CardContent,
  Typography,
  IconButton,
  Tabs,
  Tab,
  Box,
  Stack,
} from '@mui/material';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import axios from 'axios';

interface Book {
  title: string;
  cover_url: string;
  category: string;
}

interface LibraryViewProps {}

const LibraryView: React.FC<LibraryViewProps> = ({}) => {
  const [selectedTab, setSelectedTab] = useState('All');
  const [books, setBooks] = useState<Book[]>([]);

  const handleChange = (event: React.ChangeEvent<{}>, newValue: string) => {
    setSelectedTab(newValue);
  };

  const filteredBooks =
    selectedTab === 'All'
      ? books
      : books.filter((book) => book.category === selectedTab);

  useEffect(() => {
    axios
      .get('http://localhost:3000/books-new')
      .then((response) => {
        console.log(response.data);
        setBooks(
          response.data.books.map((book: any) => ({
            title: book.title,
            cover_url: book.cover_url, // Map 'cover_url' to 'image'
            category: book.category,
          }))
        );
      })
      .catch((error) => console.error('Error fetching books:', error));
  }, []);

  return (
    <Stack
      direction="column"
      spacing={1}
      sx={{ width: 1000, height: 900, backgroundColor: 'white' }}
    >
      <Tabs
        value={selectedTab}
        onChange={handleChange}
        variant="scrollable"
        scrollButtons="auto"
      >
        <Tab label="All" value="All" />
        {Array.from(new Set(books.map((book) => book.category))).map(
          (category) => (
            <Tab key={category} label={category} value={category} />
          )
        )}
      </Tabs>
      <Grid container spacing={2}>
        {filteredBooks.map((book, index) => (
          <Grid item xs={12} sm={6} md={4} lg={2.4} key={index}>
            <Card>
              <CardActionArea
                onClick={() => {
                  // onPlay(book)
                }}
              >
                <CardMedia
                  component="img"
                  height="190"
                  image={book.cover_url}
                  alt={book.title}
                  sx={{ position: 'relative' }}
                />
                <IconButton
                  sx={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)',
                    color: 'white',
                    fontSize: 'large',
                  }}
                  aria-label="play"
                >
                  <PlayCircleOutlineIcon fontSize="inherit" />
                </IconButton>
              </CardActionArea>
              <CardContent>
                <Typography gutterBottom variant="body2" component="div">
                  {book.title}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Stack>
  );
};

export default LibraryView;
